@use "./mixin.scss" as m;

.comp-hourly {
  .section-inner {
    overflow: auto;
    padding-top: 0 !important;
    h2 {
      position: sticky;
      left: 0;
    } //h2
    @media screen and (min-width: 900px) {
      display: flex;
      flex-direction: column;
    } //.section-inner min 900
    @media screen and (max-width: 900px) {
    } //.section-inner max 900
    .wrap {
      position: relative;
      display: inline-table;

      @media screen and (min-width: 900px) {
        height: 100%;
      } //wrap min 900
      @media screen and (max-width: 900px) {
        height: 400px;
      } //wrap max 900
      ul {
        display: inline-flex;
        height: 100%;
        li {
          padding-top: 20px;
          width: 70px;
          box-sizing: border-box;
          height: 100%;
          &:nth-child(even) {
            background: rgba(#fff, 0.35);
          }
          p {
            margin: 3px 0;
            font-size: 0.9em;
            text-align: center;
            img {
              display: inline-block;
              width: 80%;
            }
          }
        } //li
      } //ul
      .chart-wrap {
        position: absolute;
        left: 5px;
        bottom: 0;
        width: calc(100% - 35px);
        height: 50%;
      } //.chart-wrap
    } //wrap
  } //.section-inner
} //.comp-hourly
