@media screen and (max-width: 900px) {
  section.comp-current {
    margin-top: 30px !important;
  }
}
section.comp-current .section-inner .current-output {
  position: relative;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgb(175, 194, 175);
  background-size: cover;
  background-position: center;
  color: #fff;
}
@media screen and (min-width: 900px) {
  section.comp-current .section-inner .current-output {
    height: 100%;
  }
}
@media screen and (max-width: 900px) {
  section.comp-current .section-inner .current-output {
    padding: 10px;
  }
}
section.comp-current .section-inner .current-output button.reload {
  position: absolute;
  z-index: 1;
  width: 50px;
  height: 50px;
  right: 5%;
  top: 10%;
  font-size: 1.6em;
  opacity: 0.7;
  transition: all 0.3s;
}
section.comp-current .section-inner .current-output button.reload:active {
  opacity: 1;
  animation: rotate-reload 2s 1 forwards;
}
@keyframes rotate-reload {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(180deg);
  }
}
section.comp-current .section-inner .current-output address {
  overflow: hidden;
  margin-bottom: 10px;
  width: min(90%, 350px);
  height: 25px;
  overflow: hidden;
}
section.comp-current .section-inner .current-output address span {
  display: block;
  white-space: pre-line;
  font-weight: 500;
  animation: marquee 4s linear infinite;
}
@keyframes marquee {
  0% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(-100%);
  }
}
section.comp-current .section-inner .current-output .circle-container {
  position: relative;
  width: min(85%, 300px);
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
section.comp-current .section-inner .current-output .circle-container::before {
  content: "";
  display: block;
  padding-top: 100%;
}
section.comp-current .section-inner .current-output .circle-container .sunset {
  position: absolute;
  width: 100%;
  height: 50%;
  left: 0;
  top: 0;
}
section.comp-current .section-inner .current-output .circle-container .sunset svg {
  width: 100%;
}
section.comp-current .section-inner .current-output .circle-container .sunset svg .sun {
  fill: rgb(255, 196, 0);
  offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
  animation: rotateSun 6s linear infinite alternate;
}
@keyframes rotateSun {
  0% {
    transform: rotate(0deg) scale(0.6);
  }
  100% {
    transform: rotate(360deg) scale(0.6);
  }
}
section.comp-current .section-inner .current-output .circle-container .sunset svg .line {
  fill: none;
  stroke: rgba(255, 255, 255, 0.5);
  stroke-width: 3px;
}
section.comp-current .section-inner .current-output .circle-container .sunset svg .dash {
  fill: none;
  stroke: rgb(255, 196, 0);
  stroke-width: 6px;
  stroke-dasharray: 1;
}
section.comp-current .section-inner .current-output .circle-container .sunset time {
  font-size: 0.85em;
  padding: 3px 10px;
  text-transform: uppercase;
  background: rgb(255, 196, 0);
  border-radius: 10px;
  display: inline-block;
}
section.comp-current .section-inner .current-output .circle-container .sunset time.rise-time {
  transform: translateX(-30%);
}
section.comp-current .section-inner .current-output .circle-container .sunset time.set-time {
  float: right;
  transform: translateX(30%);
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: 90%;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp em {
  font-size: 2.6em;
  margin-right: -50px;
  position: relative;
  z-index: 1;
  text-shadow: 1px 1px 0 var(--color);
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp em sup {
  font-size: 0.7em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp img {
  width: 80px;
  mask-image: url(../assets/img/mask.png);
  mask-size: 100%;
  mask-position: center;
  mask-repeat: no-repeat;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp span {
  position: absolute;
  margin-top: 30px;
  transform: translate(40px, 10px);
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp span i {
  margin-right: 5px;
  font-size: 0.5em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .temp span b {
  font-size: 0.5em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .desc {
  text-align: center;
  font-size: 1.2em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .etc {
  text-align: center;
  width: 70%;
  margin: 5px auto;
  box-sizing: border-box;
  border-radius: 25px;
  padding: 4px;
  background: rgba(0, 0, 0, 0.15);
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .etc span {
  display: inline-block;
  margin: 2px 4px;
  font-size: 0.9em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .etc span i {
  margin-right: 2px;
  font-size: 0.8em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .etc span i.fa-location-arrow {
  margin-right: 7px;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .etc span small {
  font-size: 0.8em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .time-date {
  margin-top: -2px;
  font-size: 0.9em;
  text-align: center;
  color: white;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .time-date time {
  display: block;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .time-date time.time {
  font-size: 1.1em;
}
section.comp-current .section-inner .current-output .circle-container .circle-container-inner .time-date time small {
  font-size: 0.8em;
}