@use "./mixin.scss" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 30px !important;
  } //900이하

  .section-inner {
    .current-output {
      position: relative;
      box-sizing: border-box;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: rgb(175, 194, 175);
      background-size: cover;
      background-position: center;
      color: #fff;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //900이상 current-output
      @media screen and (max-width: 900px) {
        padding: 10px;
      } //900이하 current-output
      button.reload {
        position: absolute;
        z-index: 1;
        width: 50px;
        height: 50px;
        right: 5%;
        top: 10%;
        font-size: 1.6em;
        opacity: 0.7;
        transition: all 0.3s;
        &:active {
          opacity: 1;
          animation: rotate-reload 2s 1 forwards;
        }
        @keyframes rotate-reload {
          0% {
            transform: rotate(0deg);
          }
          100% {
            transform: rotate(180deg);
          }
        }
      } //reload
      address {
        overflow: hidden;
        margin-bottom: 10px;
        width: min(90%, 350px);
        height: 25px;
        overflow: hidden;
        span {
          display: block;
          white-space: pre-line;
          font-weight: 500;
          animation: marquee 4s linear infinite;
        } //span
        @keyframes marquee {
          0% {
            transform: translateY(0);
          }
          80% {
            transform: translateY(-100%);
          }
          100% {
            transform: translateY(-100%);
          }
        }
      } //address
      .circle-container {
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        background: rgba(#000, 0.1);
        @include m.square;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            width: 100%;
            .sun {
              fill: rgb(255, 196, 0);
              offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
              animation: rotateSun 6s linear infinite alternate;
              @keyframes rotateSun {
                0% {
                  transform: rotate(0deg) scale(0.6);
                }
                100% {
                  transform: rotate(360deg) scale(0.6);
                }
              }
            } //sun path
            .line {
              fill: none;
              stroke: rgba(255, 255, 255, 0.5);
              stroke-width: 3px;
            } //line path
            .dash {
              fill: none;
              stroke: rgb(255, 196, 0);
              stroke-width: 6px;
              stroke-dasharray: 1;
            } //dash path
          } //svg
          time {
            font-size: 0.85em;
            padding: 3px 10px;
            text-transform: uppercase;
            background: rgb(255, 196, 0);
            border-radius: 10px;
            display: inline-block;
            &.rise-time {
              transform: translateX(-30%);
            }
            &.set-time {
              float: right;
              transform: translateX(30%);
            }
          } //time
        } //.sunset
        .circle-container-inner {
          @include m.posLT(absolute, 50%, 50%, -50%, -48%);
          width: 90%;
          .temp {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            em {
              font-size: 2.6em;
              margin-right: -50px;
              position: relative;
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup {
                font-size: 0.7em;
              } //sup
            } //em
            img {
              width: 80px;
              mask-image: url(../assets/img/mask.png);
              mask-size: 100%;
              mask-position: center;
              mask-repeat: no-repeat;
            } //img
            span {
              position: absolute;
              margin-top: 30px;
              transform: translate(40px, 10px);
              i {
                margin-right: 5px;
                font-size: 0.5em;
              } //i
              b {
                font-size: 0.5em;
              }
            } //span
          } //temp
          .desc {
            text-align: center;
            font-size: 1.2em;
          } //.desc
          .etc {
            text-align: center;
            width: 70%;
            margin: 5px auto;
            box-sizing: border-box;
            border-radius: 25px;
            padding: 4px;
            background: rgba(#000, 0.15);
            span {
              display: inline-block;
              margin: 2px 4px;
              font-size: 0.9em;
              i {
                margin-right: 2px;
                font-size: 0.8em;
                &.fa-location-arrow {
                  margin-right: 7px;
                } //.fa-location-arrow
              } //i
              small {
                font-size: 0.8em;
              } //small
            } //span
          } //etc
          .time-date {
            margin-top: -2px;
            font-size: 0.9em;
            text-align: center;
            color: white;
            time {
              display: block;
              &.time {
                font-size: 1.1em;
              }
              small {
                font-size: 0.8em;
              }
            } //time
          } //time-date
        } //.circle-container-inner
      } //.circle-container
    } //.current-output div
  } //.section-inner div
} //section.comp-current section
